.move-list {
    width: 100%;

    border-radius: 3px;

    .move-head {
        background-color: rgba(211, 211, 211, 0.4);
        padding: 5px 10px;
    }

    .move-list-container {
        border-radius: 3px;

        max-height: 300px;
        overflow-y: auto;
    }

    .move-container {
        margin-block-end: 2px;
        background-color: rgba(211, 211, 211, 0.192);
        padding: 5px 10px;


        .move-amount {
            margin-block: 5px;

            font-weight: 500;

            .bitcoin {
                color: rgb(80, 227, 80);
            }

            .dollar {
                color: rgb(159, 159, 159);
            }

        }

        .move-time {
            color: rgb(185, 185, 185);
        }
    }
}